
import XHeader from 'vux/src/components/x-header/index.vue'

import Collapse from '@/components/Collapse/Collapse'
import backTop from '@/components/BackTop/backTop'
import ShowMore from '@/components/ShowMore/showMore'
export default{
components: {
XHeader,Collapse,backTop,ShowMore
},
data() {
return {
HQJCID: "",
info: {},
}
},
mounted() {
this.getParams();
},
methods: {

getParams() {
var routerParams = this.$route.params;
this.HQJCID = routerParams.HQJCID;
this.getPremaritalCheckDetail();
},

getPremaritalCheckDetail() {
this.$vux.loading.show({
text: '加载中...'
});
this.$ajax.post('',{
"MSH.1": "marriageDiagnosisService",
"MSH.2": "getById",
"HQJCID": this.HQJCID,
'token':this.$root.loginInfo.token,
}).then(res => {
this.$vux.loading.hide();
console.log(res);
if (typeof res != 'undefined') {
this.info = res;
}
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
})
},
}
}




